import axios from 'axios';
import React from 'react';
import { useState,useEffect } from 'react';
import { IoIosAdd  } from "react-icons/io";
import { ToastContainer, toast } from 'react-toastify';


function AddTodoList({userToken,options,newTodoList,setNewTodoList}) {
   
    const defaultText = '' 
    const [placeholderValue,setPlaceholderValue] = useState(defaultText) // 
    const [addText,setAddText] = useState('') // 
    

    // listnewList
    const addListBtn =(e)=>{
        // 
        if ( addText.trim() == '' && ' '){
            setAddText('');
            setPlaceholderValue('');
        }
        else {
            // 
            const addTodoListApi = "https://todoo.5xcamp.us/todos"
            const config = {
                headers:{
                    'Authorization':userToken,
                    'Content-Type':'application/json'
                }
            }
            const addList = {
                todo:{
                    content:addText,
                }
            }
            axios.post(addTodoListApi,addList,config)
            .then(response=>{
                // APInewList
                let newContent = response.data.content;
                let newId = response.data.id;
                // newList
                let newList = [...newTodoList,{id:newId,content:newContent,completed_at:null}];
                setNewTodoList(newList)
                // 
                toast.success('',options);
                // 
                let jsonList = JSON.stringify(newList)
                localStorage.setItem('listData',jsonList);
                // 
                setAddText(''); 
                // 
                setPlaceholderValue(defaultText); 
            })
            .catch((error)=>{
                toast.error(` !`,options)
            })
        } 
    }
    

      return (
          <div>
          <div className="inputBox">
          <input type="text" id="setText" 
            className={placeholderValue == '' ? 'checkStyle':''} 
            placeholder={placeholderValue == ' ' ? setPlaceholderValue(''):placeholderValue} 
            value={addText} 
            onChange={(e)=>{setAddText(e.target.value)}} 
            />
                  <a className="icon1" onClick={(e)=>{addListBtn(e)}}  >
                    <IoIosAdd />
                  </a>
              </div>
              <ToastContainer style={{color:'red'}} pauseOnFocusLoss={false}/>
          </div>
      )
}

export default AddTodoList;