import React from 'react'
import { Link, useNavigate } from 'react-router-dom'
import { useForm } from 'react-hook-form'
import axios from 'axios'
import { useState, useEffect } from 'react'

import { ToastContainer, toast } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'

import { RiEyeFill } from 'react-icons/ri'
import { RiEyeOffFill } from 'react-icons/ri'

function Register() {
    const {
        register,
        handleSubmit,
        watch,
        formState: { errors, isDirty },
    } = useForm()
    const navigate = useNavigate() // 
    const onePassword = watch('pwd') // useForm1
    const twoPassword = watch('pwd2') // useForm2
    const [str1, setSer1] = useState('') // useForm1 ()
    const [str2, setSer2] = useState('') // useForm2 ()
    const [checkError, setCheckError] = useState([]) // 
    const [iconShow, setIconShow] = useState(0) // 
    const [passwordType, sePasswordType] = useState('password') // 
    const [iconShow2, setIconShow2] = useState(0) // 2
    const [passwordType2, sePasswordType2] = useState('password') // 2

    // 
    const options = {
        position: 'bottom-right',
        autoClose: 1500,
        hideProgressBar: false,
        newestOnTop: false,
        rtl: false,
        pauseOnHover: false,
        draggable: false,
        closeOnClick: false,
    }

    useEffect(() => {
        // 6 
        if (onePassword?.length >= 6 && twoPassword?.length >= 6) {
            setSer1(onePassword)
            setSer2(twoPassword)
        }
    }, [onePassword, twoPassword, str1, str2, errors])

    // 
    const userRegister = (data) => {
        // 
        if (data.pwd === data.pwd2) {
            const api = 'https://todoo.5xcamp.us/users'
            const password = data.pwd
            // api
            const json = {
                user: {
                    email: data.email,
                    nickname: data.nickname,
                    password: password,
                },
            }
            const config = {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
            }
            axios
                .post(api, json, config)
                .then((response) => {
                    toast.success(
                        `${response.data.nickname}${response.data.message}`,
                        options
                    )
                    // alert(`${response.data.nickname}${response.data.message}`)
                    setTimeout(() => {
                        // 
                        navigate('/')
                    }, 2500)
                })
                .catch((error) => {
                    toast.error(`${error.response.data.message}${error.response.data.error[0]}`, options)
                    // alert(`${error.response.data.message}${error.response.data.error[0]}`)
                })
        }
    }

    // 
    const toggleBtn = () => {
        if (iconShow == 0) {
            // console.log('')
            setIconShow(1)
            sePasswordType('text')
        }
        if (iconShow == 1) {
            // console.log('')
            setIconShow(0)
            sePasswordType('password')
        }
    }

    // 2
    const toggleBtn2 = () => {
        if (iconShow2 == 0) {
            // console.log('')
            setIconShow2(1)
            sePasswordType2('text')
        }
        if (iconShow2 == 1) {
            // console.log('')
            setIconShow2(0)
            sePasswordType2('password')
        }
    }

    return (
        <div id='signUpPage' className='bg-yellow'>
            <div className='conatiner signUpPage vhContainer'>
                <div className='side'>
                    <a href='#'>
                        <img className='logoImg' src='https://upload.cc/i1/2022/03/23/rhefZ3.png' alt='' />
                    </a>
                    <img className='d-m-n' src='https://upload.cc/i1/2022/03/23/tj3Bdk.png' alt='workImg' />
                </div>
                <div>
                    <form className='formControls' onSubmit={handleSubmit(userRegister)}>
                        <h2 className='formControls_txt'></h2>
                        <label className='formControls_label' htmlFor='email'>
                            Email
                        </label>
                        <input
                            className='formControls_input'
                            type='text'
                            id='email'
                            name='email'
                            placeholder=' email'
                            {...register('email', {
                                required: { value: true, message: '' },
                                pattern: { value: /^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/g, message: ' Email ' },
                            })}
                        />
                        <span>{errors.email?.message}</span>
                        <label className='formControls_label' htmlFor='nickname'>
                            
                        </label>
                        <input
                            className='formControls_input'
                            type='text'
                            name='nickname'
                            id='nickname'
                            placeholder=''
                            {...register('nickname', { required: { value: true, message: '' } })}
                        />
                        <span>{errors.nickname?.message}</span>
                        <label className='formControls_label' htmlFor='pwd'>
                            
                            <i className='icon-show' onClick={toggleBtn}>
                                {iconShow == 0 ? <RiEyeOffFill size={'25px'} /> : <RiEyeFill size={'25px'} />}
                            </i>
                        </label>
                        <input
                            className='formControls_input'
                            type={passwordType}
                            name='pwd'
                            id='pwd'
                            placeholder=''
                            {...register('pwd', {
                                required: { value: true, message: '' },
                                minLength: { value: 6, message: '' },
                            })}
                        />
                        <span>{errors.pwd?.message}</span>

                        <label className='formControls_label' htmlFor='pwd2'>
                            
                            <i className='icon-show' onClick={toggleBtn2}>
                                {iconShow2 == 0 ? <RiEyeOffFill size={'25px'} /> : <RiEyeFill size={'25px'} />}
                            </i>
                        </label>
                        <input
                            className='formControls_input username'
                            type={passwordType2}
                            name='pwd2'
                            id='pwd2'
                            placeholder=''
                            {...register('pwd2', {
                                required: { value: true, message: '' },
                                minLength: { value: 6, message: '' },
                            })}
                        />
                        <span>
                            {errors.pwd2?.message
                                ? errors.pwd2?.message
                                : str1 == str2
                                ? ''
                                : ''}
                        </span>
                        <input
                            className='formControls_btnSubmit'
                            type='submit'
                            value=''
                            disabled={Object.entries(errors).length > 0 ? isDirty : !isDirty}
                            style={{
                                backgroundColor:
                                    Object.entries(errors).length > 0 || str1 !== str2 ? '#ADADAD' : '#333333',
                            }}
                        />
                        <Link className='formControls_btnLink' to='/'>
                            
                        </Link>
                    </form>
                </div>
                <ToastContainer />
            </div>
        </div>
    )
}

export default Register
