import React from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { useForm } from 'react-hook-form';
import { useState } from 'react';
import axios from 'axios';
import { useEffect } from 'react';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

import TodoList from './todoList';
import { sha256, sha224 } from 'js-sha256'; //
import Swal from 'sweetalert2';
import withReactContent from 'sweetalert2-react-content';

import { RiEyeFill } from 'react-icons/ri';
import { RiEyeOffFill } from 'react-icons/ri';

function Login() {
  const {
    register,
    handleSubmit,
    watch,
    formState: { errors },
  } = useForm(); // 
  const [iconShow, setIconShow] = useState(0); // 
  const [passwordType, sePasswordType] = useState('password'); // 
  const navigate = useNavigate(); // 
  const [userCheckToken, setUserCheckToken] = useState(''); // 'userToken'
  const [userCheckName, setUserCheckName] = useState(''); // ''
  const [userCheckEmail, setUserCheckEmail] = useState(''); // 'email'
  const [userCheckHistoryEmail, setUserCheckHistoryEmail] = useState(''); // email
  const MySwal = withReactContent(Swal); // 

  const getListApi = 'https://todoo.5xcamp.us/todos'; //  TodoList api

  // 
  const showPassword = (e) => {
    e.target.checked == true
      ? sePasswordType('text')
      : sePasswordType('password');
  };

  // 
  const options = {
    position: 'bottom-right',
    autoClose: 1500,
    hideProgressBar: false,
    newestOnTop: false,
    rtl: false,
    pauseOnHover: false,
    draggable: false,
    closeOnClick: false,
  };

  //  ()
  const loginCheck = async (data) => {
    const loginApi = 'https://todoo.5xcamp.us/users/sign_in'; //  api
    const checkData = {
      user: data,
    };
    const config = {
      headers: {
        'Content-Type': 'application/json',
      },
    };
    await axios
      .post(loginApi, checkData, config)
      .then((response) => {
        // token
        const getToken = response.headers.authorization;
        const getNickname = response.data.nickname;
        const getEmail = response.data.email;
        const success = response.data.message;

        // userToken & nickname localStorage
        localStorage.setItem('userToken', getToken);
        localStorage.setItem('nickname', getNickname);
        localStorage.setItem('email', getEmail);

        let checkOneEmail = sha256(getEmail); // 
        localStorage.setItem('checkName', checkOneEmail);

        //  () indexid
        let checkEmail = localStorage.getItem(`${checkOneEmail}`);
        let newCheckEmail = JSON.parse(checkEmail || '1234');

        // e email
        if (newCheckEmail[0] == checkOneEmail) {
          const config = {
            headers: {
              'Content-Type': 'application/json',
              Authorization: getToken,
            },
          };

          axios
            .get(getListApi, config)
            .then((response) => {
              // 
              const list = response.data.todos;
              // email
              let newEList = [];
              newCheckEmail.forEach((value) => {
                newEList[value.index] = value.id;
              });
              // 
              let filterDeleteUndefined = newEList.filter(function (value) {
                return value !== undefined;
              });

              //  indexid index
              let test = [];
              filterDeleteUndefined.forEach((value1, index1) => {
                list.forEach((value) => {
                  if (value1 == value.id) {
                    test[index1] = { ...value };
                  }
                });
              });
              const newList = JSON.stringify(test);

              // localStorage
              localStorage.setItem('listData', newList);
              // console.log('()');

              let timerInterval;
              Swal.fire({
                title: '',
                html: ' <b></b>',
                timer: 1000,
                timerProgressBar: true,
                didOpen: () => {
                  Swal.showLoading();
                  const b = Swal.getHtmlContainer().querySelector('b');
                  timerInterval = setInterval(() => {
                    b.textContent = Swal.getTimerLeft();
                  }, 100);
                },
                willClose: () => {
                  clearInterval(timerInterval);
                },
              }).then((result) => {
                if (result.dismiss === Swal.DismissReason.timer) {
                  Swal.fire({
                    title: '!',
                    html: ` ${getNickname} ${success}`,
                    timer: 1500,
                    timerProgressBar: true,
                    showConfirmButton: false,
                  }).then((result) => {
                    /* Read more about handling dismissals below */
                    if (result.dismiss === Swal.DismissReason.timer) {
                      // 
                      navigate('/todoList');
                    }
                  });
                }
              });
            })
            .catch((error) => {
              toast.error(
                `${error.response.data.message}`,
                options
              );
            });
        } else {
          // e
          const config = {
            headers: {
              'Content-Type': 'application/json',
              Authorization: getToken,
            },
          };
          axios
            .get(getListApi, config)
            .then((response) => {
              // console.log('',response)
              // 
              const list = response.data.todos;
              // console.log(list)
              const newList = JSON.stringify(list);
              // 
              // console.log('',newList)
              localStorage.setItem('listData', newList);
              // console.log('')
              let timerInterval;
              Swal.fire({
                title: '',
                html: ' <b></b>',
                timer: 1000,
                timerProgressBar: true,
                didOpen: () => {
                  Swal.showLoading();
                  const b = Swal.getHtmlContainer().querySelector('b');
                  timerInterval = setInterval(() => {
                    b.textContent = Swal.getTimerLeft();
                  }, 100);
                },
                willClose: () => {
                  clearInterval(timerInterval);
                },
              }).then((result) => {
                if (result.dismiss === Swal.DismissReason.timer) {
                  Swal.fire({
                    title: '!',
                    html: ` ${getNickname} ${success}`,
                    timer: 1500,
                    timerProgressBar: true,
                    showConfirmButton: false,
                  }).then((result) => {
                    /* Read more about handling dismissals below */
                    if (result.dismiss === Swal.DismissReason.timer) {
                      // 
                      navigate('/todoList');
                    }
                  });
                }
              });
            })
            .catch((error) => {
              toast.error(
                `${error.response.data.message}`,
                options
              );
            });
        }
      })
      .catch((error) => {
        toast.error(
          `${error.response.data.message}`,
          options
        );
        // alert(`${error.response.data.message}`)
      });
  };

  useEffect(() => {
    let newToken = localStorage.getItem('userToken');
    let newNickname = localStorage.getItem('nickname');
    let newCheckEmail = localStorage.getItem('checkName');
    let newEmail = localStorage.getItem('email');
    setUserCheckToken(newToken);
    setUserCheckName(newNickname);
    setUserCheckHistoryEmail(newCheckEmail);
    setUserCheckEmail(newEmail);
  }, []);

  // 
  const signOut = (e) => {
    MySwal.fire({
      title: '?',
      text: '',
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: '',
      cancelButtonText: '',
    }).then((result) => {
      // console.log(result)
      // 
      if (result.isConfirmed) {
        const signOutApi = 'https://todoo.5xcamp.us/users/sign_out';
        const config = {
          headers: {
            Authorization: userCheckToken,
            'Content-Type': 'application/json',
          },
        };
        axios
          .delete(signOutApi, config)
          .then((response) => {
            // localStorage
            // listData
            let listData = localStorage.getItem('listData');
            let jsonList = JSON.parse(listData);
            // indexid
            let newEmail = sha256(userCheckEmail);
            let sequenceList = [newEmail];
            jsonList.forEach((value, index) => {
              // console.log(value)
              sequenceList.push({
                index: index,
                id: value.id,
              });
            });
            //  localStorage  (  )
            let newLocalIndex = JSON.stringify(sequenceList);
            localStorage.setItem(`${newEmail}`, newLocalIndex);

            localStorage.removeItem('userToken');
            localStorage.removeItem('nickname');
            localStorage.removeItem('email');
            localStorage.removeItem('listData');

            Swal.fire(
              '!',
              '',
              'success',
              // 
              navigate(0)
            );
          })
          .catch((error) => {
            alert(`${error.response.data.message}`);
          });
      }
    });
  };

  // 
  const toggleBtn = () => {
    if (iconShow == 0) {
      // console.log('')
      setIconShow(1);
      sePasswordType('text');
    }
    if (iconShow == 1) {
      // console.log('')
      setIconShow(0);
      sePasswordType('password');
    }
  };

  return (
    <div id='loginPage' className='bg-yellow'>
      <div className='conatiner loginPage vhContainer '>
        <div className='side'>
          <a href='#'>
            <img
              className='logoImg'
              src='https://upload.cc/i1/2022/03/23/rhefZ3.png'
              alt=''
            />
          </a>
          <img
            className='d-m-n'
            src='https://upload.cc/i1/2022/03/23/tj3Bdk.png'
            alt='workImg'
          />
        </div>
        <div>
          {!userCheckToken ? (
            <form className='formControls' onSubmit={handleSubmit(loginCheck)}>
              <h2 className='formControls_txt'></h2>
              <label className='formControls_label' htmlFor='email'>
                Email
              </label>
              <input
                className='formControls_input'
                type='text'
                id='email'
                name='email'
                placeholder=' email'
                {...register('email', {
                  required: { value: true, message: '' },
                  pattern: {
                    value: /^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/g,
                    message: ' Email ',
                  },
                })}
              />
              <span>{errors.email?.message}</span>
              <label className='formControls_label' htmlFor='password'>
                
                <i className='icon-show' onClick={toggleBtn}>
                  {iconShow == 0 ? (
                    <RiEyeOffFill size={'25px'} />
                  ) : (
                    <RiEyeFill size={'25px'} />
                  )}
                </i>
              </label>
              <input
                className='formControls_input'
                type={passwordType}
                name='password'
                id='password'
                placeholder=''
                {...register('password', {
                  required: { value: true, message: '' },
                  minLength: { value: 6, message: '' },
                })}
              />
              <span>{errors.password?.message}</span>
              <input
                className='formControls_btnSubmit'
                type='submit'
                value=''
              />
              <Link className='formControls_btnLink' to='/register'>
                
              </Link>
              <br />
            </form>
          ) : (
            <form className='formControls'>
              <div
                style={{
                  width: '115%',
                  alignItems: 'center',
                  justifyContent: 'center',
                }}
              >
                <h2 className='formControls_txt'></h2>
                <label className='formControls_label' htmlFor='email'></label>
                <input
                  className='userNameShow'
                  type='text'
                  id='email'
                  name='email'
                  placeholder={`${userCheckName}`}
                  disabled
                />
              </div>
              <input
                className='formControls_btnSubmit'
                type='button'
                value=''
                onClick={signOut}
              />
              <Link className='formControls_btnLink' to='/todoList'>
                
              </Link>
            </form>
          )}
        </div>
        <ToastContainer pauseOnFocusLoss={false} />
      </div>
    </div>
  );
}

export default Login;
